'use client';

import { useState, useEffect } from 'react';
import Link from 'next/link';
import { 
  PlusIcon, 
  PencilIcon, 
  TrashIcon, 
  EyeIcon,
  ArrowPathIcon,
  ArrowsUpDownIcon,
  ChevronDownIcon
} from '@heroicons/react/24/outline';
import { useAuth } from '@/contexts/AuthContext';

// تعريف واجهة بيانات الخدمة
interface Service {
  id: string;
  title: string;
  description: string;
  category: string;
  price: number;
  status: 'active' | 'pending' | 'rejected' | 'paused';
  createdAt: string;
  impressions: number;
  clicks: number;
  orders: number;
  rating: number;
  image: string;
}

export default function ServicesPage() {
  const { user } = useAuth();
  const [isLoading, setIsLoading] = useState(true);
  const [services, setServices] = useState<Service[]>([]);
  const [filteredServices, setFilteredServices] = useState<Service[]>([]);
  const [searchQuery, setSearchQuery] = useState('');
  const [statusFilter, setStatusFilter] = useState<string>('all');
  const [categoryFilter, setCategoryFilter] = useState<string>('all');
  const [sortBy, setSortBy] = useState<{field: keyof Service | ''; direction: 'asc' | 'desc'}>({field: 'createdAt', direction: 'desc'});
  const [showDeleteModal, setShowDeleteModal] = useState(false);
  const [serviceToDelete, setServiceToDelete] = useState<string | null>(null);

  useEffect(() => {
    // محاكاة جلب بيانات الخدمات من API
    const fetchServices = async () => {
      setIsLoading(true);
      
      const mockServices: Service[] = [
        {
          id: '1',
          title: 'تصميم صور بذكاء اصطناعي بأسلوب فني مميز',
          description: 'تصميم صور إبداعية باستخدام أحدث تقنيات الذكاء الاصطناعي مع تخصيص كامل حسب طلبك',
          category: 'توليد صور',
          price: 75,
          status: 'active',
          createdAt: '2024-03-15',
          impressions: 1240,
          clicks: 320,
          orders: 48,
          rating: 4.9,
          image: '/images/services/ai-art.jpg'
        },
        {
          id: '2',
          title: 'كتابة محتوى إبداعي باستخدام GPT-4',
          description: 'كتابة مقالات ومحتوى تسويقي احترافي مع تحسين SEO باستخدام أحدث نماذج الذكاء الاصطناعي',
          category: 'كتابة محتوى',
          price: 120,
          status: 'active',
          createdAt: '2024-02-22',
          impressions: 980,
          clicks: 210,
          orders: 35,
          rating: 4.8,
          image: '/images/services/content-writing.jpg'
        },
        {
          id: '3',
          title: 'برمجة روبوت محادثة مخصص لموقعك',
          description: 'تطوير روبوت محادثة ذكي باستخدام تقنيات الذكاء الاصطناعي لتحسين تجربة العملاء',
          category: 'برمجة',
          price: 350,
          status: 'pending',
          createdAt: '2024-03-18',
          impressions: 420,
          clicks: 110,
          orders: 0,
          rating: 0,
          image: '/images/services/chatbot.jpg'
        },
        {
          id: '4',
          title: 'تدريب نموذج ذكاء اصطناعي مخصص لعملك',
          description: 'إنشاء وتدريب نماذج ذكاء اصطناعي مخصصة حسب احتياجات مشروعك مع دعم فني كامل',
          category: 'تدريب نماذج',
          price: 500,
          status: 'paused',
          createdAt: '2024-02-05',
          impressions: 650,
          clicks: 140,
          orders: 12,
          rating: 4.7,
          image: '/images/services/ml-training.jpg'
        },
        {
          id: '5',
          title: 'تحليل بيانات متقدم باستخدام الذكاء الاصطناعي',
          description: 'تحليل البيانات واستخراج الرؤى والتنبؤات باستخدام خوارزميات الذكاء الاصطناعي المتقدمة',
          category: 'تحليل بيانات',
          price: 300,
          status: 'rejected',
          createdAt: '2024-03-01',
          impressions: 0,
          clicks: 0,
          orders: 0,
          rating: 0,
          image: '/images/services/data-analysis.jpg'
        }
      ];
      
      // تأخير لمحاكاة استجابة الشبكة
      setTimeout(() => {
        setServices(mockServices);
        setFilteredServices(mockServices);
        setIsLoading(false);
      }, 1000);
    };
    
    fetchServices();
  }, []);

  // تطبيق البحث والفلترة والترتيب
  useEffect(() => {
    let result = [...services];
    
    // تطبيق البحث
    if (searchQuery) {
      result = result.filter(service => 
        service.title.includes(searchQuery) || 
        service.description.includes(searchQuery)
      );
    }
    
    // تطبيق فلتر الحالة
    if (statusFilter !== 'all') {
      result = result.filter(service => service.status === statusFilter);
    }
    
    // تطبيق فلتر الفئة
    if (categoryFilter !== 'all') {
      result = result.filter(service => service.category === categoryFilter);
    }
    
    // تطبيق الترتيب
    if (sortBy.field) {
      result.sort((a, b) => {
        if (sortBy.field === '') return 0;
        
        const valueA = a[sortBy.field];
        const valueB = b[sortBy.field];
        
        // المقارنة حسب نوع القيمة
        if (typeof valueA === 'string' && typeof valueB === 'string') {
          return sortBy.direction === 'asc' 
            ? valueA.localeCompare(valueB) 
            : valueB.localeCompare(valueA);
        } else if (typeof valueA === 'number' && typeof valueB === 'number') {
          return sortBy.direction === 'asc' 
            ? valueA - valueB 
            : valueB - valueA;
        }
        
        return 0;
      });
    }
    
    setFilteredServices(result);
  }, [services, searchQuery, statusFilter, categoryFilter, sortBy]);

  // الحصول على قائمة الفئات الفريدة من الخدمات
  const categories = Array.from(new Set(services.map(service => service.category)));

  // تغيير الترتيب
  const handleSort = (field: keyof Service) => {
    setSortBy(prev => ({
      field,
      direction: prev.field === field && prev.direction === 'asc' ? 'desc' : 'asc'
    }));
  };

  // التعامل مع حذف خدمة
  const handleDeleteClick = (serviceId: string) => {
    setServiceToDelete(serviceId);
    setShowDeleteModal(true);
  };

  const confirmDelete = () => {
    if (serviceToDelete) {
      // محاكاة حذف الخدمة
      setServices(prev => prev.filter(service => service.id !== serviceToDelete));
      setShowDeleteModal(false);
      setServiceToDelete(null);
    }
  };

  // الحصول على الباج المناسب للحالة
  const getStatusBadge = (status: Service['status']) => {
    const badges = {
      active: 'bg-green-100 text-green-800',
      pending: 'bg-yellow-100 text-yellow-800',
      rejected: 'bg-red-100 text-red-800',
      paused: 'bg-gray-100 text-gray-800'
    };
    
    const labels = {
      active: 'نشط',
      pending: 'قيد المراجعة',
      rejected: 'مرفوض',
      paused: 'متوقف'
    };
    
    return (
      <span className={`inline-flex items-center px-2.5 py-0.5 rounded-full text-xs font-medium ${badges[status]}`}>
        {labels[status]}
      </span>
    );
  };

  if (isLoading) {
    return (
      <div className="min-h-screen flex items-center justify-center">
        <div className="w-12 h-12 rounded-full border-4 border-indigo-300 border-t-indigo-600 animate-spin"></div>
      </div>
    );
  }

  return (
    <div className="space-y-6">
      <div className="flex flex-col md:flex-row justify-between items-center mb-6">
        <h1 className="text-2xl font-bold text-gray-900">خدماتي</h1>
        
        <Link href="/dashboard/services/new" 
              className="mt-4 md:mt-0 inline-flex items-center px-4 py-2 border border-transparent text-sm font-medium rounded-md shadow-sm text-white bg-indigo-600 hover:bg-indigo-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500">
          <PlusIcon className="ml-2 h-5 w-5" />
          إضافة خدمة جديدة
        </Link>
      </div>
      
      {/* إحصائيات سريعة */}
      <div className="grid grid-cols-1 sm:grid-cols-2 lg:grid-cols-4 gap-6 mb-6">
        <div className="bg-white rounded-lg border border-gray-200 shadow-sm p-5">
          <div className="flex items-center">
            <div className="flex-shrink-0 bg-indigo-100 rounded-md p-3">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-indigo-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M13 16h-1v-4h-1m1-4h.01M21 12a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div className="mr-4">
              <p className="text-sm font-medium text-gray-500">إجمالي الخدمات</p>
              <p className="text-2xl font-bold text-gray-900">{services.length}</p>
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-lg border border-gray-200 shadow-sm p-5">
          <div className="flex items-center">
            <div className="flex-shrink-0 bg-green-100 rounded-md p-3">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-green-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12l2 2 4-4M7.835 4.697a3.42 3.42 0 001.946-.806 3.42 3.42 0 014.438 0 3.42 3.42 0 001.946.806 3.42 3.42 0 013.138 3.138 3.42 3.42 0 00.806 1.946 3.42 3.42 0 010 4.438 3.42 3.42 0 00-.806 1.946 3.42 3.42 0 01-3.138 3.138 3.42 3.42 0 00-1.946.806 3.42 3.42 0 01-4.438 0 3.42 3.42 0 00-1.946-.806 3.42 3.42 0 01-3.138-3.138 3.42 3.42 0 00-.806-1.946 3.42 3.42 0 010-4.438 3.42 3.42 0 00.806-1.946 3.42 3.42 0 013.138-3.138z" />
              </svg>
            </div>
            <div className="mr-4">
              <p className="text-sm font-medium text-gray-500">الخدمات النشطة</p>
              <p className="text-2xl font-bold text-gray-900">{services.filter(s => s.status === 'active').length}</p>
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-lg border border-gray-200 shadow-sm p-5">
          <div className="flex items-center">
            <div className="flex-shrink-0 bg-yellow-100 rounded-md p-3">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-yellow-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M12 8v4l3 3m6-3a9 9 0 11-18 0 9 9 0 0118 0z" />
              </svg>
            </div>
            <div className="mr-4">
              <p className="text-sm font-medium text-gray-500">طلبات هذا الشهر</p>
              <p className="text-2xl font-bold text-gray-900">{services.reduce((sum, service) => sum + service.orders, 0)}</p>
            </div>
          </div>
        </div>
        
        <div className="bg-white rounded-lg border border-gray-200 shadow-sm p-5">
          <div className="flex items-center">
            <div className="flex-shrink-0 bg-blue-100 rounded-md p-3">
              <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6 text-blue-600" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M15 12a3 3 0 11-6 0 3 3 0 016 0z" />
                <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M2.458 12C3.732 7.943 7.523 5 12 5c4.478 0 8.268 2.943 9.542 7-1.274 4.057-5.064 7-9.542 7-4.477 0-8.268-2.943-9.542-7z" />
              </svg>
            </div>
            <div className="mr-4">
              <p className="text-sm font-medium text-gray-500">المشاهدات</p>
              <p className="text-2xl font-bold text-gray-900">{services.reduce((sum, service) => sum + service.impressions, 0)}</p>
            </div>
          </div>
        </div>
      </div>
      
      {/* أدوات البحث والفلترة */}
      <div className="bg-white rounded-lg border border-gray-200 shadow-sm p-5 mb-6">
        <div className="grid grid-cols-1 md:grid-cols-4 gap-4">
          {/* حقل البحث */}
          <div>
            <label htmlFor="search" className="block text-sm font-medium text-gray-700 mb-1">بحث</label>
            <div className="relative">
              <div className="absolute inset-y-0 right-0 pr-3 flex items-center pointer-events-none">
                <svg className="h-5 w-5 text-gray-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor" aria-hidden="true">
                  <path fillRule="evenodd" d="M8 4a4 4 0 100 8 4 4 0 000-8zM2 8a6 6 0 1110.89 3.476l4.817 4.817a1 1 0 01-1.414 1.414l-4.816-4.816A6 6 0 012 8z" clipRule="evenodd" />
                </svg>
              </div>
              <input
                type="text"
                id="search"
                className="focus:ring-indigo-500 focus:border-indigo-500 block w-full pr-10 sm:text-sm border-gray-300 rounded-md"
                placeholder="البحث في الخدمات..."
                value={searchQuery}
                onChange={(e) => setSearchQuery(e.target.value)}
              />
            </div>
          </div>
          
          {/* فلتر الحالة */}
          <div>
            <label htmlFor="status" className="block text-sm font-medium text-gray-700 mb-1">الحالة</label>
            <select
              id="status"
              className="mt-1 block w-full py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              value={statusFilter}
              onChange={(e) => setStatusFilter(e.target.value)}
            >
              <option value="all">جميع الحالات</option>
              <option value="active">نشط</option>
              <option value="pending">قيد المراجعة</option>
              <option value="paused">متوقف</option>
              <option value="rejected">مرفوض</option>
            </select>
          </div>
          
          {/* فلتر الفئة */}
          <div>
            <label htmlFor="category" className="block text-sm font-medium text-gray-700 mb-1">الفئة</label>
            <select
              id="category"
              className="mt-1 block w-full py-2 px-3 border border-gray-300 bg-white rounded-md shadow-sm focus:outline-none focus:ring-indigo-500 focus:border-indigo-500 sm:text-sm"
              value={categoryFilter}
              onChange={(e) => setCategoryFilter(e.target.value)}
            >
              <option value="all">جميع الفئات</option>
              {categories.map(category => (
                <option key={category} value={category}>{category}</option>
              ))}
            </select>
          </div>
          
          {/* زر إعادة التعيين */}
          <div className="flex items-end">
            <button
              type="button"
              className="inline-flex items-center px-4 py-2 border border-gray-300 shadow-sm text-sm font-medium rounded-md text-gray-700 bg-white hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500"
              onClick={() => {
                setSearchQuery('');
                setStatusFilter('all');
                setCategoryFilter('all');
                setSortBy({field: 'createdAt', direction: 'desc'});
              }}
            >
              <ArrowPathIcon className="ml-2 h-4 w-4 text-gray-500" />
              إعادة التعيين
            </button>
          </div>
        </div>
      </div>
      
      {/* جدول الخدمات */}
      <div className="bg-white rounded-lg border border-gray-200 shadow-sm overflow-hidden">
        <div className="overflow-x-auto">
          <table className="min-w-full divide-y divide-gray-200">
            <thead className="bg-gray-50">
              <tr>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  الخدمة
                </th>
                <th 
                  scope="col" 
                  className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('category')}
                >
                  <div className="flex items-center">
                    الفئة
                    <ArrowsUpDownIcon className="mr-1 h-4 w-4" />
                  </div>
                </th>
                <th 
                  scope="col" 
                  className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('price')}
                >
                  <div className="flex items-center">
                    السعر
                    <ArrowsUpDownIcon className="mr-1 h-4 w-4" />
                  </div>
                </th>
                <th 
                  scope="col" 
                  className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('status')}
                >
                  <div className="flex items-center">
                    الحالة
                    <ArrowsUpDownIcon className="mr-1 h-4 w-4" />
                  </div>
                </th>
                <th 
                  scope="col" 
                  className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('impressions')}
                >
                  <div className="flex items-center">
                    المشاهدات
                    <ArrowsUpDownIcon className="mr-1 h-4 w-4" />
                  </div>
                </th>
                <th 
                  scope="col" 
                  className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider cursor-pointer"
                  onClick={() => handleSort('orders')}
                >
                  <div className="flex items-center">
                    الطلبات
                    <ArrowsUpDownIcon className="mr-1 h-4 w-4" />
                  </div>
                </th>
                <th scope="col" className="px-6 py-3 text-right text-xs font-medium text-gray-500 uppercase tracking-wider">
                  <span className="sr-only">إجراءات</span>
                </th>
              </tr>
            </thead>
            <tbody className="bg-white divide-y divide-gray-200">
              {filteredServices.length > 0 ? (
                filteredServices.map((service) => (
                  <tr key={service.id} className="hover:bg-gray-50">
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="flex items-center">
                        <div className="flex-shrink-0 h-10 w-10 rounded-md overflow-hidden">
                          <div className="h-full w-full bg-gray-200 flex items-center justify-center">
                            {service.image ? (
                              <img src={service.image} alt={service.title} className="h-full w-full object-cover" />
                            ) : (
                              <span className="text-xs text-gray-500">{service.title.charAt(0)}</span>
                            )}
                          </div>
                        </div>
                        <div className="mr-4">
                          <div className="text-sm font-medium text-gray-900 line-clamp-1">{service.title}</div>
                          <div className="text-sm text-gray-500 line-clamp-1">{service.description}</div>
                        </div>
                      </div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm text-gray-900">{service.category}</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      <div className="text-sm font-medium text-gray-900">{service.price} ر.س</div>
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap">
                      {getStatusBadge(service.status)}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {service.impressions}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                      {service.orders}
                    </td>
                    <td className="px-6 py-4 whitespace-nowrap text-left text-sm font-medium">
                      <div className="flex justify-end space-x-3 space-x-reverse">
                        <Link href={`/services/${service.id}`}
                              className="text-indigo-600 hover:text-indigo-900">
                          <EyeIcon className="h-5 w-5" aria-hidden="true" />
                          <span className="sr-only">عرض</span>
                        </Link>
                        <Link href={`/dashboard/services/edit/${service.id}`}
                              className="text-blue-600 hover:text-blue-900">
                          <PencilIcon className="h-5 w-5" aria-hidden="true" />
                          <span className="sr-only">تعديل</span>
                        </Link>
                        <button
                          onClick={() => handleDeleteClick(service.id)}
                          className="text-red-600 hover:text-red-900">
                          <TrashIcon className="h-5 w-5" aria-hidden="true" />
                          <span className="sr-only">حذف</span>
                        </button>
                      </div>
                    </td>
                  </tr>
                ))
              ) : (
                <tr>
                  <td colSpan={7} className="px-6 py-4 text-center text-sm text-gray-500">
                    لا توجد خدمات مطابقة للبحث.
                  </td>
                </tr>
              )}
            </tbody>
          </table>
        </div>
      </div>
      
      {/* تلميحات ونصائح */}
      <div className="bg-blue-50 border border-blue-200 rounded-lg p-4">
        <div className="flex">
          <div className="flex-shrink-0">
            <svg className="h-5 w-5 text-blue-400" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 20 20" fill="currentColor">
              <path fillRule="evenodd" d="M18 10a8 8 0 11-16 0 8 8 0 0116 0zm-7-4a1 1 0 11-2 0 1 1 0 012 0zM9 9a1 1 0 000 2v3a1 1 0 001 1h1a1 1 0 100-2v-3a1 1 0 00-1-1H9z" clipRule="evenodd" />
            </svg>
          </div>
          <div className="mr-3">
            <h3 className="text-sm font-medium text-blue-800">نصائح لزيادة طلبات خدماتك:</h3>
            <div className="mt-2 text-sm text-blue-700">
              <ul className="list-disc mr-4 space-y-1">
                <li>استخدم صوراً جذابة وعالية الجودة لخدماتك.</li>
                <li>اكتب وصفاً تفصيلياً يوضح قيمة خدمتك وما يميزها.</li>
                <li>قدم سعراً تنافسياً مع الحفاظ على جودة الخدمة.</li>
                <li>استجب سريعاً لاستفسارات العملاء المحتملين.</li>
              </ul>
            </div>
          </div>
        </div>
      </div>
      
      {/* مودال تأكيد الحذف */}
      {showDeleteModal && (
        <div className="fixed z-10 inset-0 overflow-y-auto">
          <div className="flex items-center justify-center min-h-screen pt-4 px-4 pb-20 text-center sm:block sm:p-0">
            <div className="fixed inset-0 bg-gray-500 bg-opacity-75 transition-opacity" aria-hidden="true"></div>
            
            <span className="hidden sm:inline-block sm:align-middle sm:h-screen" aria-hidden="true">&#8203;</span>
            
            <div className="inline-block align-bottom bg-white rounded-lg text-right overflow-hidden shadow-xl transform transition-all sm:my-8 sm:align-middle sm:max-w-lg sm:w-full">
              <div className="bg-white px-4 pt-5 pb-4 sm:p-6 sm:pb-4">
                <div className="sm:flex sm:items-start">
                  <div className="mx-auto flex-shrink-0 flex items-center justify-center h-12 w-12 rounded-full bg-red-100 sm:mx-0 sm:h-10 sm:w-10">
                    <svg className="h-6 w-6 text-red-600" xmlns="http://www.w3.org/2000/svg" fill="none" viewBox="0 0 24 24" stroke="currentColor" aria-hidden="true">
                      <path strokeLinecap="round" strokeLinejoin="round" strokeWidth="2" d="M12 9v2m0 4h.01m-6.938 4h13.856c1.54 0 2.502-1.667 1.732-3L13.732 4c-.77-1.333-2.694-1.333-3.464 0L3.34 16c-.77 1.333.192 3 1.732 3z" />
                    </svg>
                  </div>
                  <div className="mt-3 text-center sm:mt-0 sm:mr-4 sm:text-right">
                    <h3 className="text-lg leading-6 font-medium text-gray-900">حذف الخدمة</h3>
                    <div className="mt-2">
                      <p className="text-sm text-gray-500">
                        هل أنت متأكد أنك تريد حذف هذه الخدمة؟ لا يمكن التراجع عن هذا الإجراء.
                      </p>
                    </div>
                  </div>
                </div>
              </div>
              <div className="bg-gray-50 px-4 py-3 sm:px-6 sm:flex sm:flex-row-reverse">
                <button
                  type="button"
                  className="w-full inline-flex justify-center rounded-md border border-transparent shadow-sm px-4 py-2 bg-red-600 text-base font-medium text-white hover:bg-red-700 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-red-500 sm:ml-3 sm:w-auto sm:text-sm"
                  onClick={confirmDelete}
                >
                  حذف
                </button>
                <button
                  type="button"
                  className="mt-3 w-full inline-flex justify-center rounded-md border border-gray-300 shadow-sm px-4 py-2 bg-white text-base font-medium text-gray-700 hover:bg-gray-50 focus:outline-none focus:ring-2 focus:ring-offset-2 focus:ring-indigo-500 sm:mt-0 sm:mr-3 sm:w-auto sm:text-sm"
                  onClick={() => setShowDeleteModal(false)}
                >
                  إلغاء
                </button>
              </div>
            </div>
          </div>
        </div>
      )}
    </div>
  );
} 